import React, { useState } from 'react';
import '../styles/faq.css';

const FaqComponent = () => {
  const [activeIndex, setActiveIndex] = useState(null);

  const faqs = [
    {
      question: 'Cmo puedo hacer parte de Colivri?',
      answer: 'Para hacerte miembro del Laboratorio Colivri tienes varias alternativas:\n' +
        '- Hacer parte de uno de los grupos de estudiantes que trabajan aqu, como OASIS o SinfonIA,\n' +
        '- Hacer parte de los grupos de investigacin que trabajan aqu, como el grupo IMAGINE,\n' +
        '- Hacer proyectos en reas afines a las trabajadas en el laboratorio (XR, VR, AR, MR, robtica, procesamiento de imgenes, multimedia, videojuegos, etc).\n' +
        'Nota: Los proyectos que puedes hacer en Colivri, contemplan desde el proyecto para ExpoAndes, proyecto de mitad de carrera (PMC), proyecto de grado o proyectos independientes asesorados por algn profesor.'
    },
    {
      question: 'Cmo puedo pedir equipos en prstamos?',
      answer: 'Para pedir equipos debes ser miembro activo de Colivri, esto significa que debes haber solicitado acceso al laboratorio con tu carnet y estar en un proyecto en curso o ser parte de un grupo de investigacin o de estudiantes (IMAGINE, OASIS, SinfonIA, etc) '
    },
    {
      question: 'Cmo solicito acceso al laboratorio y a los prstamos?',
      answer: 'Para solicitar acceso debes enviar un correo a la Coordinadora del Laboratorio, la profesora Vivian Gmez (vn.gomez@uniandes.edu.co), indicando tu nombre completo, coreo, cdigo, proyecto en curso y profesor responsable/asesor.\n' +
      'Si participas como miembro de un grupo de estudiantes (OASIS o SinfonIA), los lderes del grupo solicitarn tu acceso.'
    },
    {
      question: 'En qu cursos de ISIS puedo hacer proyectos en Colivri?',
      answer: 'Si eres estudiante de Ingeniera de Sistemas y Computacin, puedes hacer proyectos desde tu curso de Introduccin a la Ingeniera de Sistemas y en tu curso de Diseo de Productos e Innovacin en TI (ISIS2007)\n' +
      'Adicionalmente, como parte de la Maestra en Ingeniera de Sistemas y Computacin (MISIS), en los cursos Desarrollo de aplicaciones de Realidad Mixta (ISIS4814) y Computacin Visual Interactiva (ISIS4823) \n' +
      'Nota: Estos cursos tambin pueden tomarse desde el pregrado, como electivas profesionales.'
    },
    {
      question: 'Si no soy de sistemas puedo hacer parte?',
      answer: 'Claro que s! Puedes hacer parte de los grupos OASIS o SinfonIA, para ello debes estar atento(a) a sus convocatorias internas. Adicionalmente, puedes hacer proyectos si estos son de reas afines al laboratorio.'
    },
  ];

  const toggleFAQ = (index) => {
    setActiveIndex(activeIndex === index ? null : index);
  };

  return (
    <div className="faq-container col-md-9 col-sm-12">
      <h2>Preguntas Frecuentes</h2>
      {faqs.map((faq, index) => (
        <div className={`faq-item ${activeIndex === index ? 'active' : ''}`} key={index}>
          <button className="faq-question" onClick={() => toggleFAQ(index)}>
            {faq.question}
          </button>
          <div className="faq-answer" style={{ display: activeIndex === index ? 'block' : 'none' }}>
            {faq.answer.split('\n').map((paragraph, i) => (
              <p key={i}>{paragraph}</p>
            ))}
          </div>
        </div>
      ))}
    </div>
  );
};

export default FaqComponent;
